.container{
  box-shadow: 0px 0px 16px;
}
nav{
  height: 4rem;
}
.navbar-brand{
  color: inherit !important;
  color: #053835;
  font-size: 2rem;
  
}
li a{
  color: inherit !important;
  color: #053835;
}
.background-image{
  background: url("./assets/backgroundimage.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 80vh;
}
.top-advirtise{
  background-color: #fff;
  margin-top: 2rem;
  padding: 10px;
  border-radius: 3px;
  box-shadow: 4px 6px 4px;
  width: 23%;
  color:  #053835;
  position: absolute;
  top: 10rem;
  left: 14rem;
}
.top-advirtise p{
  font-size: 14px;
}
.top-advirtise-price b{
  color: #053835;
  font-size: 18px;
}
.home-btn{
  background-color:#052141;
  padding: 8px;
  border-radius: 10px;
  color: #fff;
  text-align: center;
  width: 53%;
  margin-left: 3rem;
}
@media(max-width:376px){
  .navbar-collapse{
    background-color: #fff;
    padding: 5px;
  }
  li{
    border-bottom: 1px solid;
  }
  .top-advirtise{
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
  }
  .home-btn{
    margin-left: 5rem;
  }
  .courses{
    width: 100%;
    margin-left:0 !important;
  }
  .card{
    height: 350px;
    margin: 20px auto 0 auto;
  }
  .staf-img img{
    max-width: 286px;
  }
  .start-class{
    display: inherit !important;
  }
}

.courses{
  margin: 5rem;
  color:  #053835;
}
.courses-heading h5{
  font-size: 23px;;
  font-weight: bolder;
}

.live-session{
  margin-top: 2rem;
  margin-bottom: 3rem;
  color:#053835;
}
.live-session-card{
  margin-top: 2rem;
}
.card{
  border: none;
}
.card h5{
  font-size: 18px;
}
.card p{
  font-size: 14px;
}
.card-body p{
  padding: 0px !important;
}
.live-session-card img{
  max-height:180px;
  max-width: 100vw
}
.services{
  margin:3rem 0 0 3rem;
  color: #053835;
  
}
.services h5{
  font-size: 18px;
}
.services p{
  font-size: 14px;
}
.service-one,.service-two,.service-three{
  display: flex;
}
.services i{
  font-size: 3rem;
  padding: 5px;
}
.service-content{
  margin-left: 18px;
}

.staf{
  color:#053835;
  margin-bottom: 5rem;
}
.staf-data{
  border: 1px solid #c8c8c8;
  border-radius: 10px;
  margin-top: 1.5rem;
}
.staf-record{
  margin-top: 2rem;
  margin-left: 0rem;
  margin-bottom: 3rem;
}
.staf-img img{
  width: 600px;
  height:300px;
  border-radius: 10px;
}
.staf h5{
  font-size: 23px;
}
.staf-desc p{
  margin-top:-10px !important;
}
.staf-desc button{
  background-color:#052141;
  padding: 8px;
  border-radius: 10px;
  color: #fff;
  text-align: center;
  width: 53%;
  margin-left: 3rem;
}

.start-class{
  color:#053835;
  display: flex;
  justify-content: space-between;
  margin: 2rem;
}
.start-class p{
  margin-top: -5px;
}
.start-class h5{
  font-weight: bold;
  font-size: 23px;
}
.class-btn button{
  background-color:#052141;
  padding:14px;
  padding-left: 20px;
  text-align: center;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
  text-align: center;
}
.footer{
  color:#053835;
  margin: 3rem;
  margin-bottom: 8rem;
}
.footer-list{
  margin-top: 3rem;
  padding: 5px;
}
li{
  list-style: none;
  font-weight: 500;
}
.copy-right{
  display: flex;
  margin-top: 3rem;
}
.copy-right h5{
  font-size: 23px;
}
.copy-right p{
  font-size: 12px;
  justify-content: right;
}
.bottom-navbar{
  position: fixed;
  bottom: 0;
  box-shadow: 20px 15px 15px 20px;
  background: #fff;
  width: 100vw;
  margin-left: -6px;
}
